
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import axios from 'axios'

export default {
data() {
return {
contents: [],
navName: {},
years: new Date().getFullYear(),
select: {infos: '党员信息', acin: false},
userId: cookie.get('userId')


}
},
components: {
XHeader,
ViewBox

},
methods: {
infor() {
axios.get('/dangjian/ppartymember/queryByDepartmentId', {
params: {
departmentid: this.$store.getters.user.departmentid
}
}).then(res => {
this.contents = res.data;
}).catch(err => {
console.log('fail' + err);
})

},
nav() {
axios.get('/dangjian/pdepartment/queryById', {
params: {
departmentid: this.$store.getters.user.departmentid
}
}).then(res => {
this.navName = res.data;
}).catch(err => {
console.log('fail' + err);
})
},
judge(event) {
if (event === '1') {
return event = '男'
} else {
return event = '女'
}
}
},
mounted() {
this.infor();
this.nav();
this.judge();
}
}



